import React from 'react'
import Project from '../layouts/Project'
import { projects, social, section3Title, Links } from '../../profile'
import Skills from '../layouts/Skills'

const Works = () => {
    return (
        <>
            <div data-aos="zoom-in-up" data-aos-once="true" className="third">
                <>
                    <div className="pp-head-line mx-auto text-center">
                        <h1 id="Projects" className="red-line pp-head">{section3Title}</h1>
                    </div>
            <h1 id="Links1" className="red-line pp-head">{Links}</h1>
                </>
                <div className="row">
                {projects && projects.map((x) => 
                    <Project id={x.id} url={x.url} name={x.name} skills={x.skills} faClass={x.faClass} />
                    )}
                </div>
            </div>

            {/* <div className="third">
                <>
                    <div className="pp-head-line mx-auto text-center">
                        <h1 id="Projects" className="red-line pp-head">{}</h1>
                    </div>
                </>
                <div className="row">
                    {miscellaneous && miscellaneous.map((x) => 
                    <Project id={x.id} url={x.url} name={x.name} />
                    )}
                </div>
            </div> */}
        </>
    )
}

export default Works
